@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 300;
  src: local('Open Sans Light'), local('OpenSans-Light'), url('/fonts/opensans/opensans-light-webfont.woff') format('woff');
}
@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 400;
  src: local('Open Sans'), local('OpenSans'), url('/fonts/opensans/opensans-webfont.woff') format('woff');
}
@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 700;
  src: local('Open Sans Bold'), local('OpenSans-Bold'), url('/fonts/opensans/opensans-bold-webfont.woff') format('woff');
}
@font-face {
  font-family: 'Panton';
  font-weight: normal;
  font-style: normal;
  src: url('/fonts/panton/Panton-Regular.eot?#iefix') format('embedded-opentype'),  url('/fonts/panton/Panton-Regular.otf')  format('opentype'),
       url('/fonts/panton/Panton-Regular.woff') format('woff'), url('/fonts/panton/Panton-Regular.ttf')  format('truetype'), url('/fonts/panton/Panton-Regular.svg#Panton-Regular') format('svg');
  
}
@font-face {
  font-family: 'Panton Bold';
  src: url('/fonts/panton/Panton-Bold.eot?#iefix') format('embedded-opentype'),  url('/fonts/panton/Panton-Bold.otf')  format('opentype'),
       url('/fonts/panton/Panton-Bold.woff') format('woff'), url('/fonts/panton/Panton-Bold.ttf')  format('truetype'), url('/fonts/panton/Panton-Bold.svg#Panton-Bold') format('svg');
  font-weight: bold;
  font-style: normal;
}
@font-face {
  font-family: 'Panton Black';
  src: url('/fonts/panton/Panton-Black.ttf')  format('truetype');
  font-weight: bold;
  font-style: normal;
}

